<template>
  <div id="subject">
    <div id="content" v-html="html"></div>
    <Spinner :visible="loading" :size="'50px'"></Spinner>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Spinner from "@/components/Spinner.vue";
import { getSectionHTML } from "@/scripts/scrapers";

export default defineComponent({
  name: "Subject",
  components: { Spinner },
  mounted() {
    this.$nextTick(this.updateContent);
  },
  data() {
    return {
      loading: true,
      html: "",
    };
  },
  methods: {
    async updateContent() {
      const { classId, subjectId } = this.$route.params;
      this.html = await getSectionHTML(
        classId as string,
        "https://ocjene.skole.hr/grade/" + subjectId,
      );
      this.loading = false;
    },
  },
  watch: {
    $route() {
      this.updateContent();
    },
  },
});
</script>

<style lang="scss" scoped>
</style>
